
#app {
  font-family: 'Open Sans', 'Helvetica Neue', 'Hiragino Sans GB', 'LiHei Pro', Arial, sans-serif;
  color: #333;
}
#app .masked {
  background-image: -webkit-linear-gradient(left, #147b96, rgba(163,228,208,0.71) 25%, #147b96 50%, rgba(163,228,208,0.71) 75%, #147b96);
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  -webkit-background-size: 200% 100%;
  -webkit-animation: masked-animation 4s infinite linear;
}
#app .wrapper {
  position: absolute;
  left: 0;
  width: 320px;
  text-align: center;
  top: 50%;
  left: 50%;
  margin-left: -160px;
  margin-top: -160px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
#app .wrapper h1 {
  font-size: 25px;
  letter-spacing: 3px;
}
#app .wrapper h2 {
  color: #999;
  font-weight: normal;
  font-size: 16px;
  letter-spacing: 2px;
  margin-bottom: 30px;
}
#app .wrapper p {
  margin: 0;
  line-height: 26px;
}
#app .wrapper p a {
  font-size: 15px;
  text-decoration: none;
  color: #999;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
  letter-spacing: 2px;
}
#app .wrapper p a:hover {
  color: #f00;
}
@-webkit-keyframes masked-animation {
0% {
    background-position: 0 0;
}
100% {
    background-position: -100% 0;
}
}
@keyframes masked-animation {
0% {
    background-position: 0 0;
}
100% {
    background-position: -100% 0;
}
}


